.lb-loader, .lightbox {
  text-align: center;
  line-height: 0;
  position: absolute;
  left: 0;
}

body.lb-disable-scrolling {
  overflow: hidden;
}

.lightboxOverlay {
  z-index: 9999;
  filter: alpha(Opacity= 80);
  opacity: .8;
  background-color: #000;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.lightbox {
  width: 100%;
  z-index: 10000;
  outline: 0;
  font-weight: 400;
}

.lightbox .lb-image {
  height: auto;
  max-width: inherit;
  max-height: none;
  border: 4px solid #fff;
  border-radius: 3px;
  display: block;
}

.lightbox a img {
  border: none;
}

.lb-outerContainer {
  width: 250px;
  height: 250px;
  background-color: #fff;
  border-radius: 4px;
  margin: 0 auto;
  position: relative;
}

.lb-outerContainer:after {
  content: "";
  clear: both;
  display: table;
}

.lb-loader {
  height: 25%;
  width: 100%;
  top: 43%;
}

.lb-cancel {
  width: 32px;
  height: 32px;
  background: url("loading.46e1ff58.gif") no-repeat;
  margin: 0 auto;
  display: block;
}

.lb-nav {
  height: 100%;
  width: 100%;
  z-index: 10;
  position: absolute;
  top: 0;
  left: 0;
}

.lb-container > .nav {
  left: 0;
}

.lb-nav a {
  background-image: url("data:image/gif;base64,R0lGODlhAQABAPAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==");
  outline: 0;
}

.lb-next, .lb-prev {
  height: 100%;
  cursor: pointer;
  display: block;
}

.lb-nav a.lb-prev {
  width: 34%;
  float: left;
  filter: alpha(Opacity= 0);
  opacity: 0;
  -o-transition: opacity .6s;
  background: url("prev.dcd580d6.png") 0 48% no-repeat;
  transition: opacity .6s;
  left: 0;
}

.lb-nav a.lb-prev:hover {
  filter: alpha(Opacity= 100);
  opacity: 1;
}

.lb-nav a.lb-next {
  width: 64%;
  float: right;
  filter: alpha(Opacity= 0);
  opacity: 0;
  -o-transition: opacity .6s;
  background: url("next.86169bb8.png") right 48% no-repeat;
  transition: opacity .6s;
  right: 0;
}

.lb-nav a.lb-next:hover {
  filter: alpha(Opacity= 100);
  opacity: 1;
}

.lb-dataContainer {
  width: 100%;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  margin: 0 auto;
  padding-top: 5px;
}

.lb-dataContainer:after {
  content: "";
  clear: both;
  display: table;
}

.lb-data {
  color: #ccc;
  padding: 0 4px;
}

.lb-data .lb-details {
  width: 85%;
  float: left;
  text-align: left;
  line-height: 1.1em;
}

.lb-data .lb-caption {
  font-size: 13px;
  font-weight: 700;
  line-height: 1em;
}

.lb-data .lb-caption a {
  color: #4ae;
}

.lb-data .lb-number {
  clear: left;
  color: #999;
  padding-bottom: 1em;
  font-size: 12px;
  display: block;
}

.lb-data .lb-close {
  float: right;
  width: 30px;
  height: 30px;
  text-align: right;
  filter: alpha(Opacity= 70);
  opacity: .7;
  -o-transition: opacity .2s;
  background: url("close.e6e826de.png") 100% 0 no-repeat;
  outline: 0;
  transition: opacity .2s;
  display: block;
}

.lb-data .lb-close:hover {
  cursor: pointer;
  filter: alpha(Opacity= 100);
  opacity: 1;
}

