html {
  scroll-behavior: smooth;
}

body {
  font-family: $primaryFont;
  font-size: 16px;
  font-weight: 400;
  color: getColor('black');
  // background-color: getColor('background');
  min-height: 100vh;
  -webkit-tap-highlight-color: rgba($color: #0e1012, $alpha: 0.1);
  // scroll-behavior: smooth;

  @include desktop {
    height: 100vh;
  }
}

